/* Universal CSS */

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background-color: #e3e3e399;
}

html {
  font-size: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

/* Universal CSS */

/* Navbar */

.navbar {
  background-color: #a7a5a8a6;
}

.left-half {
  display: flex;
  gap: 1vw;
  margin: 0px 1.5vw;
  padding: 0px;
}

.left-half h6 {
  margin: 6px 3px;
  font-family: emoji;
  font-weight: 500;
}

.logo {
  height: 2rem;
  width: 2.5rem;
  align-items: center;
  margin-bottom: 0px;
  box-shadow: 1px 2px 7px 0px #6e5a5a;
  border: 1px solid #909090;
  border-radius: 9px;
}

.example input[type="text"] {
  width: 20vw;
  color: #423d34;
  height: 2rem;
  padding: 10px;
  padding-top: 10px;
  font-size: 17px;
  border: 1px solid black;
  border-right: none;
  /* border-radius: 5px; */
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  box-shadow: 1px 3px 15px 0px #7a6b6b;
  background-color: #f4f4f4;
}

.example button {
  width: 5vw;
  height: 2rem;
  background-color: #f4f4f4;
  color: black;
  color: white;
  font-size: 17px;
  padding-top: -1px;
  border: 1px solid grey;
  border-left: none;
  cursor: pointer;
  border: 1px solid black;
  /* border-left: none; */
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 7px 3px 15px 0px #7a6b6b;
}

.example button:hover {
  background-color: #d6cdcd;
  color: white;
}

.example::after {
  content: "";
  clear: both;
  display: table;
}

.right-half {
  display: flex;
  gap: 1.5vw;
  margin: 0px 2vw;
  margin-left: 0px;
}
.right-half a {
  text-decoration: none;
  color: black;
  font-family: cursive;
  font-family: inherit;
  font-size: 16px;
  /* font-style: oblique; */
  font-weight: 500;
  margin-top: 5px;
}
.right-half a:hover {
  text-decoration: underline;
  font-weight: 700;
}
.form-check-label {
  font-weight: 500;
}
/* .new {
  font-size: larger;
} */
.edit-icon {
  font-size: 22px;
  font-weight: 100;
  color: black;
  margin: 0px 4px;
}
/* .login {
  font-size: larger;
} */

/* Navbar */

/* container css */

.container {
  display: inline-block;
  background-color: white;
  width: 62%;
  height: 1280px;
  overflow-y: auto;
  /* overflow-y: hidden; */
  margin-left: 7vw;
  /* padding-bottom: 11.5vh; */
  margin-top: 20px;
  border-radius: 6px;
  /* border-top-left-radius: 6px; */
  /* border-bottom-left-radius: 6px; */
  border-right: 0.5px solid grey;
  box-shadow: -2px 4px 17px -4px black;
  padding-bottom: 4.1vh;
}
.links {
  padding: 6vh 3vw;
  padding-bottom: 0vh;
  margin-bottom: 2vh;
}
.links a {
  text-decoration: none;
  color: black;
  font-weight: 300;
  margin: 0px 10px;
  /* padding-bottom: 10px; */
  font-family: inherit;
  font-weight: 600;
  font-size: 1rem;
}
.links a:hover {
  /* text-decoration: underline; */
  font-weight: 700;
}
.hr {
  margin: 0px 2vw;
  color: #8b8b8b;
  margin-bottom: 2vh;
}

/* container css */

/* blog css */

.blog {
  display: flex;
  justify-content: space-around;
  width: 90%;
  margin: auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.bloga {
  width: 70%;
  cursor: pointer;
}
.blogb {
  width: 20%;
}
.blog-head {
  display: flex;
}
.profile-pic {
  width: 25px;
  height: 25px;
  border-radius: 13px;
}
.blog-img {
  width: 100%;
  height: 9rem;
  margin: 15px 10px;
}
.blog p {
  margin-top: 0;
  margin-left: 10px;
  font-family: cursive;
  margin-bottom: 1rem;
}
.blog h1 {
  font-size: 1.5rem;
  font-family: auto;
  font-style: revert;
  font-weight: 600;
}
.blog-body {
  margin: 4px 30px;
  text-align: left;
}
.blog-body p {
  margin-left: 0px;
  margin-top: 10px;
}

/* blog css */

/* sidebar css */

.sidebar {
  display: inline-block;
  position: absolute;
  top: 4.3rem;
  width: 26vw;
  background-color: white;
  /* height: 200vh; */
  border-radius: 6px;
  /* border-top-right-radius: 6px; */
  /* border-bottom-right-radius: 6px; */
  border-right: 0.5px solid grey;
  box-shadow: -2px 2px 36px -9px black;
  margin-left: 2vw;
}

.tren-heading {
  font-size: 1rem;
  margin: 4vh 2.2vw;
  margin-bottom: 2vh;
  margin-top: 6vh;
  font-family: inherit;
  font-weight: 600;
}

.trend-sect {
  /* display: flex; */
  margin: 10vh 2.5vw;
  cursor: pointer;
}
.trend-sect img {
  margin: 0px 5px;
  margin-left: 0px;
}
.trend-sect p {
  margin: 15px 10px;
  font-family: sans-serif;
  font-weight: 600;
}

/* sidebar css */
.form-switch .form-check-input {
  background-image: var(--bs-form-switch-bg);
  background-position: left center;
  border-radius: 2em;
  transition: background-position 0.15s ease-in-out;
  margin-top: 10px;
}

.newp-h{
  font-family: inherit;
  font-weight: 600;
  font-size: 1rem;
  margin-top: 3px;
}
.title {
  width: 68vw;
  /* height: 7rem; */
  margin: auto;
  margin-top: 3rem;
}

.title label {
  color: grey;
  font-family: math;
  /* font-size: 1.7rem; */
  font-weight: 600;
  /* padding: 20px 30px; */
}

.content {
  width: 68vw;
  /* height: 7rem; */
  margin: auto;
  margin-top: 1rem;
}

.content label {
  color: grey;
  font-family: math;
  /* font-size: 1.7rem; */
  font-weight: 600;
  /* padding: 20px 30px; */
}






