.containerl {
  padding: 16px;
  width: 50vw;
  margin: auto;
  margin-top: 10vh;
}

span.psw {
  float: right;
  padding-top: 16px;
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 300px) {
  span.psw {
    display: block;
    float: none;
  }
  .cancelbtn {
    width: 100%;
  }
}

.opt{
  display: inline-block;
  color: #96ab6c;
  text-decoration: none;
  width: fit-content;
  margin: 0vw 8.2vw;
  margin-bottom: 20px;
}

.opt:hover {
  text-decoration: underline;
  font-weight: 700;
}


.rect{
  width: 90%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2.5rem;
  margin-top: 2.5rem;
}
.rect h6{
  margin-left: 1vw;
}
.tlist{
  border: 1px solid black;
    margin: 5px 3px;
    padding: 5px 12px;
    background-color: #e9e0e0;
    border-radius: 31px;
    color: black;
    text-decoration: none;
    font: 3px;
    cursor: pointer;
}


@media only screen and (min-width:500px) and (max-width:725px){
  /* Css to applied when media query is fired */
  .newp-h{
      display: none;
  }
}
@media only screen and (min-width:250px) and (max-width:500px){
  /* Css to applied when media query is fired */
  .newp-h{
      display: none;
  }
  .logo{
    display: none;
  }
  .navbar{
    flex-direction: column;
  }
  .example input[type="text"]{
    width: 70%;
    margin: auto;
  }
  .right-half{
    margin-top: 10px;
    border-top: 2px solid white;
    padding-top: 5px;
  }
}
@media only screen and (min-width:400px) and (max-width:500px){
  /* Css to applied when media query is fired */
  .form-check{
    margin-right: 23vw;
  }
}
@media only screen and (min-width:350px) and (max-width:400px){
  /* Css to applied when media query is fired */
  .form-check{
    margin-right: 13vw;
  }
}
@media only screen and (min-width:700px) and (max-width:950px){
  .container{
    margin-left: 2vw;
  }
  .sidebar{
    width: 30%;
  }
}
@media only screen and (min-width:0px) and (max-width:750px){
  .container{
    /* display: inline-block; */
    width: 90vw;
    margin-left: 5vw;
    /* margin: auto; */
  }
  .sidebar{
    display: none;
  }
}